
import _ from 'lodash'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'


export default {
  components: {
    XInput,
    XButton,
    Box,
    Card,
    Group,
    Cell,
    Icon
  },
  methods: {
    picker () {
      let self = this
      let wx = this.$wechat
      wx.chooseImage({
        count: 1, // 9
        sizeType: ['original', 'compressed'], // 
        sourceType: ['camera'], // 
        success (res) {
          // res.localIds[0] // IDlocalIdimgsrc
          // 
          wx.uploadImage({
            localId: res.localIds[0], // IDchooseImage
            isShowProgressTips: 1, // 1
            success (res) {
              self.serverId = res.serverId // ID
            }
          })
          // 
          wx.getLocalImgData({
            localId: res.localIds[0], // localID
            success (res) {
              let localData = res.localData // localDatabase64img
              localData = localData.replace('jgp', 'jpeg')// iOS  image/jgp,
              self.ticketUrl = localData
            },
            fail (res) {
              self.alert('')
            }
          })
        }
      })
    },
    submit () {
      let self = this
      let $http = this.$http
      let fields = [
        'amount',
        'serverId'
      ]
      let data = _.merge({}, _.pick(self.$data, fields))
      if (!data.serverId) {
        self.alert('')
        return
      }
      if (!data.amount) {
        self.alert('')
        return
      }
      $http.post('a/api/bill', data).then(res => {
        let response = res.body
        self.alert(response.status.info)
      })
    },
    alert (message) {
      const alert = this.$vux.alert
      alert.show({
        title: '',
        content: message
      })
    }
  },
  data () {
    return {
      title: '',
      ticketUrl: '',
      amount: '',
      serverId: ''
    }
  }
}



